<template>
  <div class="row">
    <div class="col-md-12">
      <card>
        <div slot="header">
          <h5 class="title">ADD FACILITIES</h5>
        </div>
        <form>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">NAME</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="facilities.name"></fg-input>
            </div>
          </div>
          <div class="row">
            <label class="col-md-2 col-form-label offset-md-3 text-right">FEES</label>
            <div class="col-md-3">
              <fg-input type="text" v-model="facilities.fees"></fg-input>
            </div>
          </div>
          <div class="row">
            <div id="btnAdd" class="col-md-6 offset-md-5">
              <n-button class="button" type="info" @click.native="post();handleAdd();">ADD</n-button>
            </div>
          </div>
        </form>
      </card>
    </div>
  </div>
</template>
<script>
import swal from 'sweetalert2'

export default {
  data() {
    return {
      facilities: {
        name: "",
        fees: "",
        status: "INACTIVE"
      }
    };
  },
  methods: {
    post() {
      this.$http
        .post(
          "https://admindashboard-b2ab9.firebaseio.com/facilities.json",
          this.facilities
        )
    },
    handleAdd () {
        swal({
          title: `You added ${this.facilities.name}`,
          buttonsStyling: false,
          type: 'success',
          confirmButtonClass: 'btn btn-success btn-fill',
          onClose: () =>{
            window.location.href = `#/facilities/facilities`;
          }
        })
    }
  }
};
</script>
<style scoped>
</style>
